    AlertsContainer,
    SignUpForm,
    SignInForm,
    SideMenu,
    Popup,
  },
  computed: {
    ...mapGetters({
      profile: 'auth/profile',
      signUpVisibility: 'popups/signUpVisibility',
      signInVisibility: 'popups/signInVisibility',
    }),
  },
  mounted() {
    this.$el.querySelector('.blank').style.opacity = 0
