import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '../store'

const axios = require('axios')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Main',
    component: () => import('../views/Main.vue'),
  },
  {
    path: '/exchange',
    name: 'Exchange',
    component: () => import('../views/Exchange.vue'),
  },
  {
    path: '/debug',
    name: 'Debug',
    component: () => import('../views/Test.vue'),
  },
  {
    path: '/account',
    name: 'Profile',
    component: () => import('../views/Profile.vue'),
  },
  {
    path: '*',
    name: '404',
    component: () => import('../views/NotFound.vue'),
  },
]

/* eslint-disable */
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

router.beforeEach((to, from, next) => {
  if (to.name != from.name)
    store.dispatch('preloader/startBeforeLoading').then(next)
})

router.beforeResolve((to, from, next) => {
  axios
    .get(window.location.protocol + '//' + window.location.host + '/api/auth', {
      headers: {
        Authorization: sessionStorage.getItem('auth-token'),
        route: to.name,
      },
    })
    .then(response => {
      store.commit('auth/SET_PROFILE', {
        token: response.data.token,
        profile: response.data.profile,
      })

      next()
    })
    .catch(err => {
      if (!store.state.auth.isDemo) {
        // REMOVE ON PRODUCTION
        if (to.path.startsWith('/account') || to.path.startsWith('/exchange')) {
          router.push({ path: '/' })
          store.commit('popups/SIGN_IN', true)
        }

        sessionStorage.removeItem('auth-token')
      }

      next()
    })
})

router.afterEach((to, from, next) => {
  document.title = to.name != 'Main' ? 'Onyxian - ' + to.name : 'Onyxian'

  store.dispatch('preloader/startAfterLoading').then(next)
})

export default router
