<template>
  <div class="menu">
    <div class="menu__panel">
      <div class="menu__panel__profile">
        <div class="menu__panel__profile_email_icon">
          <font-awesome-icon icon="mail-bulk"></font-awesome-icon>
        </div>
        <div class="menu__panel__profile_email">
          <p>{{profile ? profile.email : ''}}</p>
        </div>
        <div class="menu__panel__profile_balance">
          <p>{{profile ? profile.balance : ''}}$</p>
        </div>
        <div class="menu__panel__profile_close" @click="closeMenu">
          <font-awesome-icon icon="times"></font-awesome-icon>
        </div>
      </div>
      <nav class="menu__panel__links">
        <router-link :to="links.profile"
          ><li @click="closeMenu">
            <font-awesome-icon icon="user"></font-awesome-icon>
            <p>Profile</p>
          </li></router-link
        >
        <a @click="logout"
          ><li>
            <font-awesome-icon icon="sign-out-alt"></font-awesome-icon>
            <p>Log out</p>
          </li>
        </a>
        <div class="divider"></div>
        <a @click="selectTab(0)" :href="links.wallet"
          ><li>
            <font-awesome-icon icon="credit-card"></font-awesome-icon>
            <p>Wallet</p>
          </li></a
        >
        <a @click="selectTab(1)" :href="links.deposit"
          ><li>
            <font-awesome-icon icon="chart-line"></font-awesome-icon>
            <p>Deposit</p>
          </li></a
        >
        <a @click="selectTab(2)" :href="links.withdraw"
          ><li>
            <font-awesome-icon icon="money-bill"></font-awesome-icon>
            <p>Withdraw</p>
          </li></a
        >
        <a @click="selectTab(3)" :href="links.transfer"
          ><li>
            <font-awesome-icon icon="exchange-alt"></font-awesome-icon>
            <p>Transfer</p>
          </li></a
        >
        <a @click="selectTab(4)" :href="links.history"
          ><li>
            <font-awesome-icon icon="history"></font-awesome-icon>
            <p>History</p>
          </li></a
        >
        <div class="divider"></div>
        <a @click="closeMenu" href="#"
          ><li>
            <font-awesome-icon icon="users"></font-awesome-icon>
            <p>About us</p>
          </li></a
        >
        <div class="menu__panel__footer">
          <ul class="menu__panel__footer_essentials">
            <a @click="closeMenu" href="#"
              ><font-awesome-icon icon="envelope"></font-awesome-icon
            ></a>
            <a @click="closeMenu" href="#"
              ><font-awesome-icon icon="info-circle"></font-awesome-icon
            ></a>
          </ul>
          <ul class="menu__panel__footer_privacy">
            <li><a @click="closeMenu" href="">Privacy</a></li>
            <li><a @click="closeMenu" href="">Terms</a></li>
            <li><a @click="closeMenu" href="">Cookies</a></li>
          </ul>
        </div>
      </nav>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'SideMenu',
  data() {
    return {
      links: {
        profile: '/account',
        wallet: '/exchange#wallet',
        deposit: '/exchange#deposit',
        transfer: '/exchange#transfer',
        withdraw: '/exchange#withdraw',
        history: '/exchange#history',
      },
    };
  },
  computed: {
    ...mapGetters({
      visibility: 'popups/sideMenuVisibility',
      profile: 'auth/profile',
    }),
  },
  watch: {
    visibility(yes) {
      const self = this;

      if (yes) {
        self.$el.style.display = 'block';

        this.$anime({
          targets: this.$el,
          duration: 400,
          easing: 'easeOutExpo',
          opacity: [0, 1],
        });

        this.$anime({
          targets: this.$el.querySelector('.menu__panel'),
          duration: 400,
          easing: 'easeOutExpo',
          translateX: [600, 0],
        });
      } else {
        this.$anime({
          targets: this.$el,
          duration: 400,
          easing: 'easeOutExpo',
          opacity: [1, 0],
          complete() {
            self.$el.style.display = 'none';
          },
        });

        this.$anime({
          targets: this.$el.querySelector('.menu__panel'),
          duration: 400,
          easing: 'easeInExpo',
          translateX: [0, 600],
        });
      }
    },
  },
  methods: {
    selectTab(index) {
      this.$store.commit('popups/SELECT_EXCHANGE_TAB', index);
      this.closeMenu();
    },
    closeMenu() {
      this.$store.commit('popups/SIDE_MENU', false);
    },
    logout() {
      this.$store.commit('auth/SET_PROFILE', {
        profile: undefined,
      });

      this.closeMenu();
      if (this.$router.name != 'Main') this.$router.push('/');
    },
    closeOutside(e) {
      if (
        !this.$el.contains(e.target) &&
        !e.target.classList.contains('burger')
      )
        this.closeMenu();
    },
  },
  mounted() {
    document.addEventListener('click', this.closeOutside);
  },
};
</script>

<style lang="sass" scoped>
@import "../../scss/smart-grid"
@import "../../scss/variables"

a:focus
    outline: none

.menu
    position: absolute
    right: 0
    top: 0
    height: 100%
    z-index: 10
    pointer-events: none
    opacity: 0
    display: none

    @include to(30rem)
        backdrop-filter: blur(5px)
        width: calc(100% - 20px)
        padding: 20px 10px
        background-color: darken($blue-alpha, 10)
    @include from(30rem)
        width: 350px
        padding: 20px
        background-color: transparent

    &__panel
        transform: translateX(500px)
        border-radius: 10px
        margin: 0 auto
        background-color: $blue
        width: calc(100% - 40px)
        height: calc(100% - 40px)
        overflow: hidden
        pointer-events: all

        @include to(21rem)
            width: calc(100% - 20px)

        @include from(21rem)
            width: calc(100% - 40px)

        &__profile
            display: grid
            width: 100%
            background-color: $cyan
            grid-template-columns: 70px 1fr 70px
            grid-template-rows: 50px 30px
            grid-template-areas: "email balance close" "email address close"
            justify-content: space-between
            align-items: center
            border-bottom: 1px $blue-alpha solid
            box-shadow: 0 0 10px $blue-alpha

            &_email
                color: #ffffff99
                text-align: left
                font-size: 13px
                overflow: hidden
                width: 100%
                grid-area: address
                align-self: start
                text-align: left

            &_balance
                color: #fff
                font-size: 16px
                font-weight: 800
                overflow: hidden
                width: 100%
                grid-area: balance
                align-self: end
                margin: 5px 0
                text-align: left

            &_email_icon
                color: #fff
                border-radius: 100%
                background-color: $light-blue
                height: 40px
                margin: 0 15px
                display: grid
                align-items: center
                justify-items: center
                grid-area: email

            &_close
                color: #fff
                border-radius: 100%
                height: 40px
                margin: 0 15px
                display: grid
                align-items: center
                justify-items: center
                transition: all .2s cubic-bezier(.52,.89,.83,1.55)
                grid-area: close
                cursor: pointer

                &:hover
                    background-color: $pink
                    color: white

        &__links
            display: flex
            flex-direction: column
            overflow-y: auto
            height: calc(100% - 100px)

            .divider
                width: 100%
                border-bottom: 1px solid $cyan-alpha
                margin: 10px auto

            a
                text-decoration: none
                cursor: pointer

                & *
                    color: lighten($light-blue, 10)

                &:hover *
                    color: #fff

                li
                    width: 100%
                    display: grid
                    grid-template-columns: 70px 1fr
                    grid-template-rows: 50px
                    color: white
                    align-items: center
                    justify-items: center

                    &:hover
                        background-color: $cyan

                p
                    font-family: Nunito, "Open Sans", "helvetica neue", helvetica, arial, sans-serif;
                    text-transform: uppercase;
                    justify-self: start

        &__footer
            &_privacy
                display: flex
                flex-direction: row
                justify-content: center;
                margin: 10px 0

                li
                    list-style: disc
                    color: $light-blue
                    margin: 0 10px
                    font-size: 10px

                    &:first-child
                        list-style: none

                a
                    color: $light-blue
                    padding-left: 3px

                    &:hover
                        color: white

                span
                    color: $light-blue
                    font-weight: 800
                    margin: 0 5px
                    pointer-events: none

            &_essentials
                display: flex
                justify-content: center;
                padding: 0 30px
                margin: 10px 0

                a
                    text-decoration: none
                    border-radius: 100%

                    *
                        width: 20px
                        height: 20px
                        margin: 0 10px
                        fill: $light-blue

                        &:hover
                            fill: white
</style>
